import { Component, OnInit } from '@angular/core';
import { XfinityService } from '../Xfinity.service';
import { Headers, Http, Response } from '@angular/http';

@Component({
  selector: 'app-xfinity-list',
  templateUrl: './xfinity-app.component.html',
  styleUrls: ['./xfinity-app.component.css']
})
export class XfinityAppComponent implements OnInit {

  xfinityData;

  constructor(private _xfinityService: XfinityService) {
    this.xfinityData = this._xfinityService.getXfinityData();
    console.log(this.xfinityData);

  }

  getXfinityData() {
    this._xfinityService.getXfinityData().subscribe(
      (response) => console.log(response),
      (error) => console.log(error)
    );
  }

  //sorting
  key: string = 'name'; //set default
  reverse: boolean = false;
  sort(key) {
    this.key = key;
    this.reverse = !this.reverse;
  }

  ngOnInit() {

  }


}



